define('client/router', ['exports', 'ember', 'client/config/environment'], function (exports, Ember, config) {

	'use strict';

	var Router = Ember['default'].Router.extend({
		location: config['default'].locationType
	});

	Router.map(function () {
		this.route('teste');
		this.route('outro');
	});

	exports['default'] = Router;

});